/**
 * MCP Client Service for unified AI interactions
 * Handles all communication with the MCP server
 */

export interface MCPMessage {
  role: 'user' | 'assistant' | 'system' | 'tool';
  content: string;
  tool_calls?: ToolCall[];
  tool_call_id?: string;
  name?: string;
}

export interface ToolCall {
  tool: string;
  parameters: Record<string, any>;
  id?: string;
}

export interface MCPResponse {
  success: boolean;
  messages: MCPMessage[];
  citations?: Citation[];
  metadata?: Record<string, any>;
  error?: string;
}

export interface Citation {
  document_id: string;
  document_title: string;
  chunk_id?: string;
  relevance_score: number;
  snippet: string;
}

export interface MCPToolsResponse {
  tools: Record<string, MCPToolDefinition>;
}

export interface MCPToolDefinition {
  name: string;
  description: string;
  parameters: Record<string, any>;
  returns: Record<string, any>;
}

class MCPClient {
  private baseUrl: string;
  private token: string | null;

  constructor(baseUrl?: string) {
    const envBase = (import.meta as any)?.env?.VITE_API_URL as string | undefined;

    if (baseUrl) {
      this.baseUrl = baseUrl.replace(/\/$/, '');
    } else if (envBase) {
      this.baseUrl = envBase.replace(/\/$/, '');
    } else {
      const { protocol, hostname, port } = window.location;
      const localDevPorts = new Set(['3000', '3001', '3002', '5173']);
      if (port && localDevPorts.has(port)) {
        this.baseUrl = `${protocol}//${hostname}:18000`;
      } else {
        this.baseUrl = `${protocol}//${hostname}${port ? `:${port}` : ''}`.replace(/\/$/, '');
      }
    }

    this.token = localStorage.getItem('access_token');

    if ((import.meta as any)?.env?.DEV) {
      console.debug('[MCPClient] baseUrl', this.baseUrl);
    }
  }

  /**
   * Stream a message response using Server-Sent Events
   */
  async streamMessage(
    content: string,
    options: {
      tools?: string[];
      rag_enabled?: boolean;
      session_id?: string;
      department?: string;
      uploaded_documents?: any[];
      onChunk?: (chunk: string) => void;
      onComplete?: () => void;
      onError?: (error: string) => void;
    } = {}
  ): Promise<void> {
    // Since EventSource doesn't support POST with custom headers, we'll use fetch with ReadableStream
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/stream`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.token}`
        },
        body: JSON.stringify({
          messages: [{
            role: 'user',
            content
          }],
          tools: options.tools || ['chat'],
          session_id: options.session_id,
          context: {
            rag_enabled: options.rag_enabled ?? true,
            department: options.department,
            uploaded_documents: options.uploaded_documents
          }
        })
      });

      if (!response.ok) {
        throw new Error(`Stream request failed: ${response.statusText}`);
      }

      const reader = response.body?.getReader();
      const decoder = new TextDecoder();

      if (!reader) {
        throw new Error('No response body');
      }

      let buffer = '';
      let currentEvent: string | null = null;
      let completed = false;
      let receivedChunk = false;

      while (true) {
        const { done, value } = await reader.read();

        if (done) {
          if (!completed && options.onComplete) {
            completed = true;
            options.onComplete();
          }
          break;
        }

        buffer += decoder.decode(value, { stream: true });

        const lines = buffer.split('\n');
        buffer = lines.pop() ?? '';

        for (const rawLine of lines) {
          const trimmedLine = rawLine.trim();

          if (!trimmedLine) {
            currentEvent = null;
            continue;
          }

          if (trimmedLine.startsWith('event:')) {
            currentEvent = trimmedLine.slice(6).trim();
            continue;
          }

          if (!trimmedLine.startsWith('data:')) {
            continue;
          }

          const dataStr = trimmedLine.slice(5).trim();
          if (!dataStr) {
            continue;
          }

          if (dataStr === '[DONE]') {
            if (!completed && options.onComplete) {
              completed = true;
              options.onComplete();
            }
            currentEvent = null;
            continue;
          }

          try {
            const payload: any = JSON.parse(dataStr);
            const isObject = typeof payload === 'object' && payload !== null;

            if (isObject && 'error' in payload && options.onError) {
              options.onError(payload.error);
              currentEvent = null;
              continue;
            }

            if (currentEvent === 'error' && options.onError) {
              options.onError(typeof payload === 'string' ? payload : 'Stream error');
              currentEvent = null;
              continue;
            }

            if (isObject) {
              const chunkCandidate =
                'chunk' in payload ? payload.chunk :
                typeof payload.data === 'string' ? payload.data :
                typeof payload.message === 'string' ? payload.message :
                payload.message?.content ?? payload.content ?? null;

              if (chunkCandidate !== null && chunkCandidate !== undefined && options.onChunk) {
                receivedChunk = true;
                options.onChunk(String(chunkCandidate));
              }
            } else if (typeof payload === 'string' && options.onChunk) {
              receivedChunk = true;
              options.onChunk(payload);
            }

            if ((currentEvent === 'done' || (isObject && payload.status === 'complete')) && options.onComplete && !completed) {
              completed = true;
              options.onComplete();
            }
          } catch (e) {
            if (import.meta.env?.DEV) {
              console.error('Failed to parse SSE data:', e, { rawLine, dataStr });
            }
          }
        }
      }

      if (!receivedChunk && import.meta.env?.DEV) {
        console.warn('MCP stream finished without chunks');
      }

    } catch (error) {
      console.error('Stream error:', error);
      if (options.onError) {
        options.onError(error instanceof Error ? error.message : 'Unknown error');
      }
    }
  }

  /**
   * Send a message through MCP with optional tool selection
   */
  async sendMessage(
    content: string,
    options: {
      tools?: string[];
      rag_enabled?: boolean;
      session_id?: string;
      department?: string;
      uploaded_documents?: any[];
    } = {}
  ): Promise<MCPResponse> {
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/message`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.token}`
        },
        body: JSON.stringify({
          messages: [{
            role: 'user',
            content
          }],
          tools: options.tools || ['chat'],
          session_id: options.session_id,
          context: {
            rag_enabled: options.rag_enabled ?? true,
            department: options.department,
            uploaded_documents: options.uploaded_documents
          }
        })
      });

      if (!response.ok) {
        throw new Error(`MCP request failed: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error('MCP Client Error:', error);
      return {
        success: false,
        messages: [],
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  }

  /**
   * Execute a specific MCP tool directly
   */
  async executeTool(
    tool: string,
    parameters: Record<string, any>
  ): Promise<MCPResponse> {
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/tools/${tool}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.token}`
        },
        body: JSON.stringify({
          tool,
          parameters
        })
      });

      if (!response.ok) {
        throw new Error(`Tool execution failed: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error('MCP Tool Error:', error);
      return {
        success: false,
        messages: [],
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  }

  /**
   * Search documents using hybrid search
   */
  async searchDocuments(
    query: string,
    options: {
      limit?: number;
      department?: string;
      threshold?: number;
    } = {}
  ): Promise<MCPResponse> {
    return this.executeTool('hybrid_search', {
      query,
      limit: options.limit || 5,
      department: options.department,
      threshold: options.threshold || 0.5
    });
  }

  /**
   * Get available MCP tools
   */
  async getAvailableTools(): Promise<MCPToolsResponse> {
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/tools`, {
        headers: {
          'Authorization': `Bearer ${this.token}`
        }
      });

      if (!response.ok) {
        throw new Error(`Failed to get tools: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error('Get tools error:', error);
      return { tools: {} };
    }
  }

  /**
   * Get MCP context for a session
   */
  async getContext(sessionId: string): Promise<any> {
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/context/${sessionId}`, {
        headers: {
          'Authorization': `Bearer ${this.token}`
        }
      });

      if (!response.ok) {
        throw new Error(`Failed to get context: ${response.statusText}`);
      }

      return await response.json();
    } catch (error) {
      console.error('Get context error:', error);
      return null;
    }
  }

  /**
   * Clear MCP context for a session
   */
  async clearContext(sessionId: string): Promise<boolean> {
    try {
      const response = await fetch(`${this.baseUrl}/api/v1/mcp/context/${sessionId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${this.token}`
        }
      });

      return response.ok;
    } catch (error) {
      console.error('Clear context error:', error);
      return false;
    }
  }

  /**
   * Upload document as MCP resource
   */
  async uploadDocument(
    file: File,
    metadata: {
      scope: 'GLOBAL' | 'CHAT';
      visibility: 'all' | 'department';
      session_id?: string;
    }
  ): Promise<MCPResponse> {
    // This will be implemented in Phase 2
    // For now, use existing REST endpoint
    const formData = new FormData();
    formData.append('file', file);
    formData.append('scope', metadata.scope);
    formData.append('visibility', metadata.visibility);
    if (metadata.session_id) {
      formData.append('session_id', metadata.session_id);
    }

    try {
      const response = await fetch(`${this.baseUrl}/api/v1/documents/upload`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.token}`
        },
        body: formData
      });

      if (!response.ok) {
        throw new Error(`Upload failed: ${response.statusText}`);
      }

      const result = await response.json();
      return {
        success: true,
        messages: [{
          role: 'system',
          content: `Document uploaded: ${result.document_id}`
        }],
        metadata: result
      };
    } catch (error) {
      return {
        success: false,
        messages: [],
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  }

  /**
   * Update auth token
   */
  updateToken(token: string): void {
    this.token = token;
  }
}

// Export singleton instance
export const mcpClient = new MCPClient();

// Export class for testing
export default MCPClient;



